<template>
  <div class="container-fluid">
    <div class="row">
      <h1 class='display-3'>Submission Successful!</h1>
      <p>Your submission was successful! Please see your a sales manager for approval. </p>
      <router-link tag="span" to="/">
        <a class="btn btn-primary">Submit a New Request</a>
      </router-link>
    </div>
  </div>
</template>

<script>
import { db } from "../main";
import router from "../router";

export default {
  name: "SuccessfulSubmission",
  data() {
    return {
      reqs: [],
    };
  },
  firestore() {
    return {
      reqs: db
        .collection("makeReady")
        .where("type_of_vehicle", "==", "New")
        .where("completionSales", "==", "Incomplete")
    };
  },
  methods: {
    
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

h1 {
    margin-bottom: 2rem;
}

a {
    margin-top: 2rem;
}

.container-fluid {
    width: 90%;
}

.row {
    text-align: center;
    margin: 0 auto;
    display: block;
}

.btn-primary {
  background-color: #0096a5;
  color: white!important;
}

</style>



