<template>
  <table class="table">
  <thead class="thead-dark">
    <tr>
      <th scope="col">#</th>
      <th scope="col">Sales Associate</th>
      <th scope="col">Customer Name</th>
      <th scope="col">Sales Status</th>
      <th scope="col">Parts Status</th>
      <th scope="col">Finance Status</th>
      <th scope="col">Car Wash Status</th>
      <th scope="col">Delivery Bay</th>
    </tr>
  </thead>
  <tbody>
    <tr v-for="(req, index) in reqs" :key="index">
      <th>{{index + 1}}</th>
      <td>{{req.sales_associate}}</td>
      <td>{{req.customer_name}}</td>
      <td>{{req.completionSales}}</td>
      <td>{{req.partsClaimedStatus}}</td>
      <td>{{req.completionFinance}}</td>
      <td>{{req.completionCarWash}}</td>
      <td>{{req.deliveryBay}}</td>
    </tr>
  </tbody>
</table>
</template>

<script>
import { db } from "../main";
import router from "../router";

export default {
  name: "Finance",
  data() {
    return {
      reqs: [],
    }
  },
  firestore() {
    return {
      reqs: db
        .collection("makeReady")
    };
  },
  methods: {
    dateFormat(date){
    return new Date(date.seconds * 1000).toLocaleString()
    }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



