<template>
  <div class="container-fluid">
    <div class="row">
      <div class="col-md-12">
        <h1 class="display-3">Parts Dispatch</h1>
      </div>
      <div class="card col-md-3" v-for="(req, index) in reqs" :key="index">
        <div class="card-body">
          <h5 class="card-title">Stock #: {{req.stock_number}}</h5>
          <p class="card-text">Sales Associate: {{req.sales_associate}}</p>
          <p class="card-text">Customer Name: {{req.customer_name}}</p>
          <p class="card-text">Year: {{req.year}}</p>
          <p class="card-text">Model: {{req.model}}</p>
          <p class="card-text">Type of Vehicle: {{req.type_of_vehicle}}</p>
          <p v-if="req.customer_care_package == 'Yes'" class="card-text badge yesccp">Customer Care Package: {{req.customer_care_package}}</p>
          <p v-else-if="req.customer_care_package == 'No'" class="card-text badge noccp">Customer Care Package: {{req.customer_care_package}}</p>
          <p v-if="req.accessories == 'Yes'" class="card-text badge yesccp">Accessories: {{req.accessories}}</p>
          <p v-else-if="req.accessories == 'No'" class="card-text badge noccp">Accessories: {{req.accessories}}</p>
          <p class="card-text" v-if="req.accessories == 'Yes'">List Accessories: {{req.list_accessories}}</p>
          <p class="card-text">Parts Associate: {{req.partsClaimedAssociate}}</p>
          <hr />
            <label v-if='req.partsClaimed' for="partsClaimedStatus">Status</label>
            <select v-if='req.partsClaimed' id="partsClaimedStatus" class="form-control form-control-lg" v-model= 'req.partsClaimedStatus' required>
              <option></option>
              <option v-for="(approval, index) in approvals" :key="index">{{ approval }}</option>
            </select>
            <hr v-if='req.partsClaimed'/>
            <label v-if='req.partsClaimed' for="partsClaimedAssociate">Parts Associate</label>
            <select v-if='req.partsClaimed' id="partsClaimedAssociate" class="form-control form-control-lg" v-model='req.partsClaimedAssociate' required>
              <option></option>
              <option v-for="(man, index) in managers" :key="index">{{ man }}</option>
            </select>
            <hr v-if='req.partsClaimed' />
            <label v-if='!req.partsClaimed' for="mats">Mats</label>
            <select v-if='!req.partsClaimed' id="mats" class="form-control form-control-lg" v-model="req.mats">
              <option></option>
              <option v-for="(mat, index) in mats" :key="index">{{ mat }}</option>
            </select>
            <label v-if='req.mats == "Rubber Mats (CCP)"' for="mats">Invoice #</label>
            <input
              v-if='req.mats == "Rubber Mats (CCP)"'
              type="text"
              class="form-control form-control-lg"
              id="stock_number"
              placeholder="ex: 123456"
              v-model="req.invoice"
              required
            />
            <button
              @click="claimed(req.id, req.partsClaimedAssociate, req.partsClaimedStatus)"
              class="btn btn-primary" v-if='req.partsClaimed'
            >Claim</button>
            <button v-if='!req.partsClaimed' @click="unclaimed(req.id, req.partsClaimedAssociate, req.partsClaimedStatus)" class="btn btn-primary">Unclaim</button>
            <button v-if='!req.partsClaimed' @click="complete(req.id, req.partsClaimedAssociate, req.partsClaimedStatus, req.mats, req.invoice)" class="btn btn-primary">Complete</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { db } from "../main";
import router from "../router";

export default {
  name: "Parts",
  data() {
    return {
      reqs: [],
      approvals: ["Claimed"],
      managers: [
        'Jason',
        'Krystal',
        'Martin',
        'Carlos',
        'Chris',
        'Red',
        'John',
        'Freddie',
        'Victor',
        'Daniel',
        'Kevin',
        'Alan',
        'Russ'
      ],
      mats: [
        'Rubber Mats (CCP)',
        'Carpet Mats'
      ],
      completionSales: "",
      salesManager: "",
      claimedStatus: "",
      carwashassociate: "",
      delivery_bay: ""
    };
  },
  firestore() {
    return {
      reqs: db
        .collection("makeReady")
        .where("query", "==", true).where("partsclaimed", "==", true)
    };
  },
  methods: {
    claimed(id, associate, claim) {
        db.collection("makeReady")
          .doc(id)
          .update({
            partsClaimedAssociate: associate,
            partsClaimedTimestamp: new Date(),
            partsClaimedStatus: claim,
            partsClaimed: false
          });
      },
      unclaimed(id, associate, claim) {
        db.collection("makeReady")
          .doc(id)
          .update({
            partsClaimedAssociate: '',
            partsClaimedTimestamp: '',
            partsClaimedStatus: '',
            partsClaimed: true
          });
      },
      complete(id, associate, claim, bay, invoice) {
        db.collection("makeReady")
          .doc(id)
          .update({
            partsclaimed: false,
            invoice: invoice,
            partsCompletionTimestamp: new Date(),
            partsClaimedStatus: 'Complete',
            mats: bay
          });
      }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1 {
  margin-top: 20px;
  margin-bottom: 30px;
}

button {
  margin-top: 15px;
}

.container-fluid {
  width: 90%;
}
</style>



